import React from "react";
import { HeadFC, HeadProps, PageProps } from "gatsby";
import { graphql } from "gatsby";
import { getImage } from "gatsby-plugin-image";

import {
  IServicesPageContext,
  IServicesPreviewQueryResult,
} from "../types/services.type";

import Border from "../assets/common/border.svg";

import Banner from "../components/common/banner";
import ContentContainer from "../components/common/contentContainer";
import ServicePreview from "../components/services/servicePreview";
import { HeadSeo } from "../components/common/metadata";
import ContactForm from "../components/common/contactFormSection";

const Services = ({
  data,
  pageContext: { markupData, lang },
}: PageProps<IServicesPreviewQueryResult, IServicesPageContext>) => {
  const services = data.allMarkdownRemark?.nodes;
  const bannerImage = getImage(markupData.image);

  return (
    <>
      <Banner
        heightClass="lg:h-99"
        title={markupData.bannerTitle}
        image={bannerImage}
      />
      <div className="bg-main-100">
        <ContentContainer className="flex-col items-center max-w-[958px]">
          <div className="columns-1 md:columns-2 w-full py-14 gap-x-20">
            {services
              ? services.map((service) => (
                  <ServicePreview service={service} key={service.id} />
                ))
              : null}
          </div>
        </ContentContainer>
      </div>
      <Border className="w-full" />
      <ContactForm lang={lang} />
    </>
  );
};

export default Services;

export const Head: HeadFC<
  IServicesPreviewQueryResult,
  IServicesPageContext
> = ({
  pageContext: { markupData, site, lang },
  location,
}: HeadProps<IServicesPreviewQueryResult, IServicesPageContext>) => (
  <>
    <HeadSeo
      title={`${markupData.head} | ${markupData.bannerTitle.toUpperCase()}`}
      siteUrl={site.siteUrl}
      path={location.pathname}
      lang={lang}
    />
  </>
);

export const query = graphql`
  query ($lang: String) {
    allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "services" }, language: { eq: $lang } }
      }
    ) {
      nodes {
        id
        html
        frontmatter {
          title
          image {
            publicURL
          }
        }
      }
    }
  }
`;
